* {
	box-sizing: border-box;
}
body {
	background-color: #ffffff;
	color: #666;
	font-family: 'Open Sans', Arial, sans-serif;
	font-size: 14px;
	font-weight: 500;
}

:root {
  --color-darkgrey: #3f3d3a;
  --color-black: #3f0210;
  --color-pink: rgb(216, 22, 82);
  --color-green: #37af65;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.body-form {
  font-family: 'Poppins', sans-serif;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.4;
  color: rgb(216, 22, 82);
  color: var(--color-pink);
  margin: 0;
  
}
.body-form::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: -1;
  /* background: var(--color-dark); */
  background-image: linear-gradient(
      115deg,
      rgba(226, 226, 247, 0.8),
      rgba(163, 163, 177, 0.7)
    ),
    url(https://www.vocesvitales.org.ar/wp-content/uploads/2019/12/Mesa-de-trabajo-117-2.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}
.title-form {
  font-weight: 600;
  line-height: 1.2;
  font-size: 2rem;
  font-family: Arial, Helvetica, sans-serif;
  font-feature-settings: "smcp";
  font-variant: small-caps;
  color: rgb(216, 22, 82);
  color: var(--color-pink);
}

.title-form,
.description, 
.description-mentoreo,
.one {
  margin-top: 0;
  margin-bottom: 0.5rem;
  font-size: 1.45rem;
}

input,
.submit-button,
select,
textarea {
  margin: 0;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}

.submit-button {
  border: none;
}

.form-banner {
  width: 100%;
  margin: 3.125rem auto 0 auto;
}
@media (min-width: 320px) {
  .form-banner {
    max-width: 300px;
  }
}
@media (min-width: 576px) {
  .form-banner {
    max-width: 540px;
  }
}

@media (min-width: 768px) {
  .form-banner {
    max-width: 720px;
  }
}

.header {
  padding: 0 0.625rem;
  margin-bottom: 1.875rem;
}

.description {
  font-style: italic;
  font-weight: bold; 
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.4);
}
.description-mentoreo{
  font-style: italic;
  font-size: 1.2rem;
  font-weight: bold;
  color: #3f0210;
  color: var(--color-black);
}

.clue {
  margin-left: 0.25rem;
  font-size: 0.9rem;
  color: #e4e4e4;
}

.text-center {
  text-align: center;
}
.one{
    color:#3f0210;
    color:var(--color-black);
}

form {
  background: rgb(216, 22, 82);
  background: var(--color-pink);
  padding: 2.5rem 0.625rem;
  border-radius: 0.25rem;
  opacity: 0.8;
}

@media (min-width: 480px) {
  form {
    padding: 2.5rem;
  }
}

.form-group {
  margin-top: 1.5rem;
  margin: 0 auto 1.25rem auto;
  padding: 0.25rem;
}

.form-control {
  display: block;
  width: 100%;
  height: 2.375rem;
  padding: 0.375rem 0.75rem;
  color: #495057;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #ced4da;
  border-radius: 0.25rem;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

.form-control:focus {
  border-color: #80bdff;
  outline: 0;
  box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
}

.input-radio,
.input-checkbox {
  display: inline-block;
  margin-right: 0.625rem;
  min-height: 1.25rem;
  min-width: 1.25rem;
}

.input-textarea {
  min-height: 120px;
  width: 100%;
  padding: 0.625rem;
  resize: vertical;
  outline:none;
}

.submit-button {
  display: block;
  width: 100%;
  padding: 0.75rem;
  background: #3f0210;
  background: var(--color-black);
  color: #fff;
  border-radius: 4px;
  cursor: pointer;
}
.carrusel{
  padding-top: 88px;
}
.about__article {
	display: flex;
	flex-direction: column;
	width: 100%;
	padding: 0 15px;
}

.about__article h3 {
	font-weight: 600;
	font-size: 20px;
	margin-top: 30px;
}

.about__article p {
	line-height: 26px;
	font-size: 16px;
	margin: 0 0 30px;
}

.about__imgbox img {
	max-width: 100%;
}

@media (min-width: 768px) {
	.about__article {
		width: 50%;
	}
}

@media (min-width: 1200px) {
	.about__article {
		width: 25%;
	}
}

.line {
	width: 50px;
	height: 3px;
	background-color: #2a2a2a;
	margin: 0 auto 30px;
}

.line--color {
	background-color: #53a59c;
}

.section__h2 {
	font-family: 'Verdana', Helvetica, Arial, Lucida, sans-serif;
	font-size: 42px;
	color: #3f3d3a;
	line-height: 1.3em;
	text-align: center;
	font-weight: normal;
}

.about {
	width: 100%;
	margin-left: auto;
	margin-right: auto;
}

.description {
	text-align: center;
	font-family: 'Verdana', Helvetica, Arial, Lucida, sans-serif;
	font-size: 15px;
	letter-spacing: 3px;
	line-height: 1.8em;
	max-width: 700px;
	margin: 15px auto 0;
	padding: 0 15px;
}

.about__columns {
	display: flex;
	flex-wrap: wrap;
	flex-direction: column;
	width: 100%;
	margin-top: 90px;
}

@media (min-width: 768px) {
	.about__columns {
		flex-direction: row;
	}
}

@media (min-width: 1200px) {
	.about {
		width: 1200px;
	}
	.about__columns {
		flex-direction: row;
	}
}

.subscribe {
	width: 100%;
	background-color: #9ad8da;
	padding: 60px 15px;
	text-align: center;
	margin: 30px 0;
}

.subscribe__title {
	color: #333333;
	margin: 0;
	padding-bottom: 15px;
	font-weight: 700;
	font-size: 20px;
	line-height: 20px;
}

#subscribe__form {
	border-top: 1px solid #797979;
	border-bottom: 1px solid #797979;
	padding: 30px 0;
}

.subscribe__form-group {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.subscribe__form-group label {
	color: #000000;
}

.required {
	color: #f71010;
}

.form__control {
	width: 100%;
	border: none;
	margin: 15px 0;
	height: 30px;
	border-radius: 4px;
}

.subscribe__button {
	margin: 0 auto;
	background-color: white;
	border: none;
	padding: 15px 30px;
	border-radius: 64px;
	margin-top: 30px;
	letter-spacing: 2px;
	color: #3f3d3a;
	font-size: 15px;
	font-weight: 700;
}

@media (min-width: 768px) {
	.subscribe__container {
		width: 70%;
		margin: auto;
	}
}

@media (min-width: 1200px) {
	.subscribe {
		border-radius: 50px;
		margin-left: -150px;
	}
	.subscribe__container {
		width: 500px;
		margin: auto;
	}
}

.partnership {
	background-color: #d2d0cd;
	padding: 60px 0;
	margin: 60px auto 0;
	text-align: center;
}

.partnership__info {
	font-family: 'Verdana', Helvetica, Arial, Lucida, sans-serif;
	font-size: 15px;
	letter-spacing: 3px;
	line-height: 1.8em;
	max-width: 700px;
	margin: 15px auto 0;
	padding: 0 15px 30px;
}

.partnership__link {
	color: #53a59c;
	font-family: 'Verdana', Helvetica, Arial, Lucida, sans-serif;
	font-size: 15px;
	letter-spacing: 3px;
	line-height: 1.8em;
}

.bottom__triangle {
	display: none;
	width: 0;
	height: 0;
	border-left: 50vw solid transparent;
	border-right: 50vw solid transparent;
	border-top: 8vw solid #d2d0cd;
}

@media (min-width: 1200px) {
	.bottom__triangle {
		display: block;
	}
}

